import React, {useState} from 'react';
import {Tabs} from '../../types/admin';
import {TabsWrapper, Wrapper} from './styles';
import {Container} from '../../ui-kit/Container';
import {Button} from '../../ui-kit/Button';
import {Typography} from '../../ui-kit/Text';
import {Type} from './Type';
import {Tag} from './Tag';
import {Speciality} from './Speciality';
import {Project} from './Project';

const tabs = [Tabs.types, Tabs.tags, Tabs.speciality, Tabs.projects];

export const Admin: React.FC = () => {
	const [tab, setTab] = useState(Tabs.types);

	const changeTabHandler = (value: Tabs) => () => {
		setTab(value);
	};

	return (
		<Container $direction="row">
			<Wrapper>
				<Typography sizeT={30} variant="h1" weight="semi-bold" color="white">{tab.toUpperCase()}</Typography>
				<TabsWrapper>
					{tabs.map((item) => <Button onClick={changeTabHandler(item)} variant={tab === item ? 'primary' : 'secondary'} width={140} key={item}>{item.toUpperCase()}</Button>)}
				</TabsWrapper>
				{tab === Tabs.types && <Type />}
				{tab === Tabs.tags && <Tag />}
				{tab === Tabs.speciality && <Speciality />}
				{tab === Tabs.projects && <Project />}
			</Wrapper>
		</Container>
	);
};
